<template>
    <ul>
      <ThirdItem2 
      v-for="(item, index) in thirdList" 
      :key="index"
      :item="item"
      v-infinite-scroll="loadMore"
      infinite-scroll-disabled="loading"
      infinite-scroll-distance="10"/>
    </ul>
</template>

<script>
  import ThirdItem2 from '@/pages/home/ThirdItem2.vue'

  export default {
    components: {
      ThirdItem2
    },
    props: {
      thirdList: Array,
      addThirdList: Function
    },
    data() {
      return {
          pageVal: 0,
          loading: false
      }
    },
    methods: {
      loadMore() {
        if (this.pageVal === 0){
          this.pageVal++
        } else {
          this.loading = true
          this.pageVal++
          this.$axios.get('getPage/' + this.pageVal,{})
          .then(res=>{
            if (res.data.code === 200) {
                this.addThirdList(res.data.data)
            } else{
                console.log('**********code is not found ***********')
                return
            }
          })
          .catch((error)=> {
              if (error.response) {
              // 请求已发出，但服务器响应的状态码不在 2xx 范围内
              // console.log(error.response.data)
              // console.log(error.response.status)
              // console.log(error.response.headers)              
                switch (error.response.status) {
                  case 416:
                    console.log(error.response.data.msg)
                    this.loading = true
                    break
                  default:
                    break
                }
              } else {
                console.log('Error', error.message)
              }
          });

          this.loading = false
        }
      }
    }           
  }
</script>


<style  lang="scss" scoped>

</style>



