<template>
    <ul>
      <li>
        <FirstItem 
        v-for="(item, index) in firstList" 
        :key="index"
        :item="item"/>
      </li>
    </ul>
</template>

<script>
  import FirstItem from '@/pages/home/FirstItem.vue'

  export default {
    components: {
      FirstItem
    },
    props: {
      firstList: Array
    }       
  }
</script>


<style  lang="scss" scoped>
  ul{
    background:rgba(255,255,255,1);

    li{
      display:flex;
      flex-wrap: wrap;
      width: 100%;
    }
  }
</style>



