<template>   
  <router-link :to="'/getOneProduct/' + item.newsid">
    <div class="img-div">
      <img v-lazy="item.thumb" alt="">     
    </div>
    <p>{{ item.title }}</p> 
    <p class="light">{{ item.success_num }}</p>                     
  </router-link>       
</template>

<script>
  export default {
    props: {
      item: Object
    }
  }
</script>


<style  lang="scss" scoped>
  a{
    flex:0 0 25%;
    height: 115px;    
    display:flex;
    flex-direction: column ;
    justify-content: center;
    align-items: center;

    .img-div{
      width: 56px;
      height: 56px;
      margin-bottom: 5px;
      
      img{
        width: 56px;
        height: 56px;
      }
    }

    p{
      font-family: MicrosoftYaHei;
      font-weight: 400;
      color:rgba(71,76,69,1);
    }

    .light{
      font-size: 12px;
      font-family: MicrosoftYaHei;
      font-weight: 400;
      color:rgba(214,214,214,1);
    }
  }
</style>



