<template>
  <div class="divide">            
    <div class="slide"></div>
    <p>{{ title }}</p>
  </div>
</template>

<script>
  export default {
    props: {
      title:''   
    }
  }
</script>


<style  lang="scss" scoped>
  .divide{    
    width: 100%;
    height: 37.5px; 
    display: flex;
    align-items: center;

    .slide{
      width: 4px;
      height: 14px;
      background:rgba(245,118,24,1);
      margin-left: 13px;
    }

    p{
      width: auto;
      height: 14px;
      font-size: 18px;
      font-family: MicrosoftYaHei;
      font-weight: 400;
      color:rgba(71,76,69,1);
      line-height: 12px;
      margin-left: 5.5px;
    }
  }  
</style>



