<template>
    <div class="broadcast">            
        <mt-swipe :auto="0">
            <mt-swipe-item v-for="(item, index) in topList" :key="index">
                <router-link :to="'/getOneProduct/' + item.link">
                    <img v-lazy="item.src" alt="">                     
                </router-link>                    
            </mt-swipe-item>
        </mt-swipe>
    </div>
</template>

<script>
// type 可以是下面原生构造器
// String
// Number
// Boolean
// Function
// Object
// Array
// Symbol
  export default {
    props: {
      topList: Array
    },
  }
</script>


<style  lang="scss" scoped>
  .broadcast{     
    .mint-swipe {
      width: 100%;
      height: 121.5px;

      .mint-swipe-item {
        img {
        width: 100%;
        height: 100%;
        }
      }
    }
  }  
</style>



