<template>
    <ul>
      <li>
        <CategoryItem 
        v-for="(item, index) in categoryList" 
        :key="index"
        :item="item"/>    
      </li>
    </ul>
</template>

<script>
  import CategoryItem from '@/pages/home/CategoryItem.vue'

  export default {
    components: {
      CategoryItem
    },
    props: {
      categoryList: Array
    }    
  }
</script>


<style  lang="scss" scoped>
  ul{
    overflow-x: auto;
    box-sizing: border-box;
    width: 100%;
    display: block;
    white-space: nowrap;
    list-style: none;
    background:rgba(255,255,255,1);

    li{
      display:flex;
      width: auto;
      height: 86px;
      align-items: center;
    }
  }

</style>



