<template>   
  <router-link :to="'/getProductCategory/' + item.link">
    <img v-lazy="item.src" alt="">
  </router-link>      
</template>

<script>
  export default {
    props: {
      item: Object
    }
  }
</script>


<style  lang="scss" scoped>
  img{
    width: 168px;
    height: 57px;
    margin: 0px 9px;
  }
</style>



